import React, { useEffect, useRef } from "react"

import testImage from '../images/test.png'

const IndexPage = () => {
  const video = useRef()

  useEffect(() => {
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: false
    })
      .then(stream => {
        video.current.srcObject = stream
      })
      .catch(err => {
        console.error(err)
      })
  }, [])

  return (
    <div>
      <video
        ref={ref => video.current = ref}
        autoPlay
        playsInline
        muted
        poster={testImage}
        // width={480}
        // height={640}
      />
    </div>
  )
}

export default IndexPage
